import React from 'react';
import { notification } from 'antd';
import { POOL_CONTRACT_ABI, TOKEN_CONTRACT_ABI } from '../constants'


const openNotification = (title: string, content: string) => {
  notification.open({
    message: title,
    description: content,
    duration: 5,
    // icon: <SmileOutlined style={{ color: '#108ee9' }} />,
  });
};

export class TokenContract {
  web3: any;
  contractAddress: string;
  contract: any;
  defaultAddress: string;
  public decimal: number;

  constructor(web3: any, contractAddress: string, from: string) {
    this.web3 = web3;
    this.contractAddress = contractAddress;
    this.contract = new web3.eth.Contract(TOKEN_CONTRACT_ABI, contractAddress, { from: from });
    this.defaultAddress = from;
    this.decimal = 0;
  }

  async updateDecimals() {
    const decimal = await this.decimals();
    this.decimal = this.decimal;
    return decimal;
  }

  async balanceOf(address: string) {
    const balance = await this.contract.methods.balanceOf(address).call();
    return balance / (10 ** this.decimal);
  }

  async totalSupply() {
    const total = await this.contract.methods.totalSupply().call();
    return total / (10 ** this.decimal);
  }

  async decimals() {
    const decimal = await this.contract.methods.decimals().call();
    this.decimal = decimal;
    return decimal;
  }

  async approve(spender: string, amount: number) {
    const value = amount * (10 ** this.decimal)

    // console.log(value.toString());
    return this.contract.methods.approve(spender, value.toString())
      .send({ from: this.defaultAddress })
      .on('transactionHash', function (payload: any) { openNotification('transaction created', `transaction hash: ${payload}`); })
      .on('receipt', function (payload: any) { openNotification('approve success', `block hash: ${payload['blockHash']}`);})
      .on('error', function (payload: any) { openNotification('approve error', `${payload['message']}`); console.log(payload) });
  }

  async allowance(owner: string, spender: string) {
    const amount = await this.contract.methods.allowance(owner, spender).call();
    return amount / (10 ** this.decimal);
  }
}


export class PoolContract {
  web3: any;
  contractAddress: string;
  contract: any;
  defaultAddress: string;

  constructor(web3: any, contractAddress: string, from: string) {
    this.web3 = web3;
    this.contractAddress = contractAddress;
    this.contract = new web3.eth.Contract(POOL_CONTRACT_ABI, contractAddress, { from: from });
    this.defaultAddress = from;
  }

  stake(amount: number, decimals: number) {
    const value = amount * (10 ** decimals);
    return this.contract.methods.stake(value.toString())
      .send({ from: this.defaultAddress })
      .on('transactionHash', function (payload: any) { openNotification('transaction created', `transaction hash: ${payload}`); })
      .on('receipt', function (payload: any) { openNotification('stake success', `block hash: ${payload['blockHash']}`); })
      .on('error', function (payload: any) { openNotification('stake error', `${payload['message']}`); console.log(payload) });
  }

  withdraw(amount: number, decimals: number) {
    const value = amount * (10 ** decimals);

    return this.contract.methods.withdraw(value.toString())
      .send()
      .on('transactionHash', function (payload: any) { openNotification('transaction created', `transaction hash: ${payload}`); })
      .on('receipt', function (payload: any) { openNotification('withdraw success', `block hash: ${payload['blockHash']}`); })
      .on('error', function (payload: any) { openNotification('withdraw error', `${payload['message']}`); console.log(payload) });
  }

  exit() {
    return this.contract.methods.exit()
      .send()
      .on('transactionHash', function (payload: any) { openNotification('transaction created', `transaction hash: ${payload}`); })
      .on('receipt', function (payload: any) { openNotification('exit success', `block hash: ${payload['blockHash']}`); })
      .on('error', function (payload: any) { openNotification('exit error', `${payload['message']}`); console.log(payload) });
  }

  async earned(userAddress: string, decimals: number) {
    const earning = await this.contract.methods.earned(userAddress).call();
    return earning / (10 ** decimals);
  }

  getReward() {
    return this.contract.methods.getReward()
      .send()
      .on('transactionHash', function (payload: any) { openNotification('transaction created', `transaction hash: ${payload}`); })
      .on('receipt', function (payload: any) { openNotification('get reward success', `block hash: ${payload['blockHash']}`); })
      .on('error', function (payload: any) { openNotification('get reward error', `${payload['message']}`); console.log(payload) });
  }

  async totalSupply(decimals: number) {
    const total = await this.contract.methods.totalSupply().call();
    return total / (10 ** decimals);
  }

  async balanceOf(userAddress: string, decimals: number) {
    const balance = await this.contract.methods.balanceOf(userAddress).call();
    return balance / (10 ** decimals);
  }
}