import React from 'react';
import { Skeleton } from 'antd';
import MediaQuery from 'react-responsive'
import styled from 'styled-components';
import { CaretRightOutlined } from '@ant-design/icons';
// import { Button } from 'antd';
import './StakeItem.css'

const Container = styled.div`
    cursor: pointer;
    padding: 16px 16px;
    overflow: hidden;
    transition: .2s background ease-out;
    align-items: center;
    border-bottom: 1px solid #181818;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    background: #2b2b2b;
    border-radius: 3px;
`;

const Logo = styled.img`
    width: 2.4rem;
    margin-right: 16px;
    display: inline-block;
`;

const Header = styled.div`
    flex-grow: 0;
    max-width: 33.333333%;
    flex-basis: 33.333333%;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: flex-start;
`;

const ColumnHeaderLeft = styled(Header)`
    max-width: 50%;
    flex-basis: 50%;
    text-align: left;
`;

const ColumnHeaderRight = styled(Header)`
    max-width: 50%;
    flex-basis: 50%;
    text-align: right;
    justify-content: flex-end;
`;

const Content = styled.div`
    flex-grow: 0;
    max-width: 16.666667%;
    flex-basis: 16.666667%;
    text-align: left;
`;

const LeftContent = styled.div`
    flex-grow: 0;
    max-width: 50%;
    flex-basis: 50%;
    text-align: right;
    padding-right: 16px;
    font-size: 14px;
`;

const RightContent = styled.div`
    flex-grow: 0;
    max-width: 50%;
    flex-basis: 50%;
    text-align: left;
    padding: 4px;
`;

const Button = styled.button`
    color: #121212;
    flex: 0 0 auto;
    padding: 12px;
    overflow: visible;
    font-size: 1.5rem;
    text-align: center;
    transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 50%;
    cursor: pointer;
    display: inline-flex;
    position: relative;
    align-items: center;
    vertical-align: middle;
    justify-content: center;
    text-decoration: none;
    background-color: transparent;
`;

interface IStakeItemProp {
    logo: string;
    title: string;
    tokens: number;
    yearRoi: number;
    value: number;
    onClick: any;
    loading: boolean;
}

class StakeItem extends React.Component<IStakeItemProp, {}> {
    constructor(props: IStakeItemProp) {
        super(props);
    }

    render() {
        return (
            <Container className="container" onClick={this.props.onClick} key={this.props.title}>
                <MediaQuery minWidth={768}>
                    <Header>
                        <Logo src={this.props.logo}></Logo>
                        <p>{this.props.title}</p>
                    </Header>
                    <Content>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 35, borderRadius: 4 }}></Skeleton.Avatar> : <p>{this.props.tokens.toLocaleString('en-US')}</p>}
                    </Content>
                    <Content>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 35, borderRadius: 4 }}></Skeleton.Avatar> : <p>{this.props.yearRoi.toLocaleString('en-US')}%</p>}
                    </Content>
                    <Content>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 35, borderRadius: 4 }}></Skeleton.Avatar> : <p>${this.props.value.toLocaleString('en-US')}</p>}
                    </Content>
                    <Content style={{ textAlign: "right" }}>
                        <Button className="button"><CaretRightOutlined /></Button>
                    </Content>
                </MediaQuery>
                <MediaQuery maxWidth={768}>
                    <ColumnHeaderLeft>
                        <Logo src={this.props.logo}></Logo>
                        <p>{this.props.title}</p>
                    </ColumnHeaderLeft>
                    <ColumnHeaderRight>
                        <Button className="button" onClick={this.props.onClick}><CaretRightOutlined /></Button>
                    </ColumnHeaderRight>
                    <LeftContent><p style={{ fontWeight: 400 }}>Tokens Deposited</p></LeftContent>
                    <RightContent>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 30, borderRadius: 4 }}></Skeleton.Avatar> : <p>{this.props.tokens.toLocaleString('en-US')}</p>}
                    </RightContent>
                    <LeftContent><p style={{ fontWeight: 400 }}>APY</p></LeftContent>
                    <RightContent>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 30, borderRadius: 4 }}></Skeleton.Avatar> : <p>{this.props.yearRoi.toLocaleString('en-US')}%</p>}
                    </RightContent>
                    <LeftContent><p style={{ fontWeight: 400 }}>Value</p></LeftContent>
                    <RightContent>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 30, borderRadius: 4 }}></Skeleton.Avatar> : <p>${this.props.value.toLocaleString('en-US')}</p>}
                    </RightContent>
                </MediaQuery>
            </Container>
        );
    }

};

export default StakeItem;
